.bill-c{
    .am-list-body{
        .am-list-item{padding-left:0;
            .am-list-line{padding-right: 0;
                .am-list-content{padding:0;}
            }
        }
        .sticky{background-color: #efefef;padding:8px 16px;
            svg{position: relative;top:4px;}
            .amount{color: #888;font-size: 12px;}
        }
        .listview-item{display: flex;position: relative;align-items: center;height:68px;
            svg{width: 36px;height: 36px;margin: 0 12px;}
            .right{flex:1;padding: 10px 12px;padding-left: 0;justify-content:space-between;height: 100%;border-bottom:1px solid #ddd;line-height:1;font-size:14px;box-sizing: border-box;
                .cate{position: absolute;left: 70px;top:12px;}
                .add-amount{color:#ff5b05;}
                .amount{position: absolute;right: 20px;top:12px;font-size: 18px;}
                .billTime{position: absolute;left: 70px;top:38px;color:#999;}
                .operator{position: absolute;left: 198px;top:38px;color:#999;}
                .consumeTime{position: absolute;left: 198px;top:12px;position: absolute;color:#888;}
                .highlight{color: orange;}
            }
            
        }
        .listview-even{background-color: #f5fcff;}
    }
    .am-list-footer{height: 110px;}
}
