:global {
  body,ul,ol,li,p,h1,h2,h3,h4,h5,h6,form,fieldset,table,td,img,div,dl,dt,dd,input{margin: 0;padding: 0;}
  body{font-size: 12px;}
  html,body{height: 100%;}
  img{border:none;}
  ul,ol,li{list-style: none;}
  input,select,textarea{outline: none;}
  textarea{resize:none;}
  a{text-decoration: none;}
  table {border-collapse: collapse;border-spacing: 0;}

  .clearfix:after{content:"";display: block;clear: both;}
  .clearfix{zoom:1;}
  .fl{float: left;}
  .fr{float: right;}
  .wrap{margin:0 auto;}
  
  .my-custom-antd-mask{position: relative;opacity: 0.9;
    #myCustomAntdMask{position:absolute;width: 20px;height: 20px;top: 0;left: 0;bottom: 0;right: 0;margin: auto;z-index: 2001;
        &:before,&:after{content:'';border-radius: 50%;background-color: #2db7f5;animation: antSpinBounce 2.2s infinite ease-in-out;display: block;position: absolute;opacity: .5;top: 0;left: 0;bottom: 0;right: 0;}
        &:after{animation-delay: -1.1s;}
    }
  }
  .login-modal{.anticon,.ant-confirm-title{display: none;}
    .ant-confirm-body .ant-confirm-content{margin-left: 0;}
    .ant-confirm-btns{display: none;}
    .login-iframe{
      iframe{width: 100%;height: 370px;}
    }
  }

  .custom-form-invalid-explain{position: absolute;bottom: 42px;right: 0px;background-color: #f7eaea;border-radius: 4px;border: 1px solid #e9c3c0;padding: 2px 6px;font-weight: 600;box-shadow: 3px 3px 8px #999;
  }
  .custom-form-invalid-explain:before{
    content:'';position: absolute;width: 0;height: 0;border-width:4px 4px 0;bottom: -5px;left: 9px;
      border-style:solid;
      border-color:#e9c3c0 transparent transparent;
  }
  .custom-form-invalid-explain:after{
    content:'';position: absolute;width: 0;height: 0;border-width:4px 4px 0;bottom: -4px;left: 9px;
      border-style:solid;
      border-color:#f7eaea transparent transparent;
  }

  .ant-drawer-body{
    .ant-collapse-content-box{padding: 6px;}
    .ant-form-item{margin-bottom: 0px;}
    .ant-row{display: flex;
        .ant-form-item{margin-bottom: 0px;
            .ant-form-item-label{width: 120px;}
            .ant-form-item-control-wrapper{width: 200px;}
        }
    }
    button.ant-btn{
        margin-top: 20px;margin-right: 20px;
    }
  }
}