.main-c{
    .am-navbar-left{width: 80px;padding-left:10px;font-size: 16px;color:#000;}
    .am-search{width: 70vw;background-color:#fff;}
    
}

.member-bill-modal{
    .am-modal-popup-slide-up{height: 60vh;}
    
}

.am-list-header{text-align: left;}
.am-list-body{
    
    .listview-item{display: flex;position: relative;align-items: center;height:68px;
        .icon{width: 42px;height: 42px;margin: 0 12px;}
        .right{flex:1;padding: 10px 12px;padding-left: 0;justify-content:space-between;height: 100%;border-bottom:1px solid #ddd;line-height:1;font-size:14px;box-sizing: border-box;
            .name{font-size: 18px;text-align: left;color:#000;width:120px; white-space:nowrap; overflow:hidden; text-overflow:ellipsis;}
            .phone{color:#888;padding: 4px 0;position: absolute;top:38px;}
            .time{position: absolute;right: 20px;top:38px;}
            .operator{position: absolute;left: 178px;top:38px;}
            .consumeTime{position: absolute;left: 178px;top:12px;position: absolute;color:#888;}
            .highlight{color: orange;}
        }
        
    }
    .listview-even{background-color: #f5fcff;}
}
.am-popover-mask{pointer-events: auto;}
.am-popover .am-popover-item-icon{margin-right: 4px;line-height:22px;
    svg{width: 16px;height: 16px;}
}
.am-swipe-actions{
    padding: 1px;
    
}



.member-add-modal{
    .image-pick{height: 100px;display:block;
        .am-image-picker-list{margin-bottom:0;}
    }
    .am-progress-outer{
        position: relative;
        top: -8px;
        left: 8px;width: 83px;
        
    }
    .btn-item{margin-top: 1px;}
    .am-button{margin-right: 10px;
        &::before{
            border: 1px solid #ccc!important;
        }
    }
    .am-button-small{font-size: 16px;    height: 34px;line-height: 34px;}
}